.home-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    text-align: center;
    position: relative;
}

.glitch-container {
    font-family: 'VT323', monospace;
    font-size: 7vw;
    font-weight: 700;
    color: #fff;
    position: relative;
    text-transform: uppercase;
}

.glitch-container:before,
.glitch-container:after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    overflow: hidden;
}

.glitch-container:before {
    left: 2px;
    text-shadow: -2px 0 #ff00ff;
    animation: glitch-skew 5s infinite linear alternate;
}

.glitch-container:after {
    left: -2px;
    text-shadow: -2px 0 #00ffff;
    animation: glitch-skew 8s infinite linear alternate-reverse;
}

@keyframes glitch-skew {
    0% { clip-path: inset(95% 0 2% 0); }
    10% { clip-path: inset(50% 0 45% 0); }
    20% { clip-path: inset(95% 0 2% 0); }
    30% { clip-path: inset(25% 0 50% 0); }
    40% { clip-path: inset(10% 0 88% 0); }
    50% { clip-path: inset(60% 0 15% 0); }
    60% { clip-path: inset(90% 0 3% 0); }
    70% { clip-path: inset(30% 0 65% 0); }
    80% { clip-path: inset(70% 0 10% 0); }
    90% { clip-path: inset(45% 0 40% 0); }
    100% { clip-path: inset(95% 0 2% 0); }
}

.glitch-cursor {
    color: #0f0;
    animation: blink 0.7s step-end infinite;
}

.subtitle {
    font-size: 1.5rem;
    color: #0f0;
    margin-top: 20px;
    text-shadow: 0 0 5px #0f0;
}
